/* All */
main {
  background-image: url("https://images.unsplash.com/photo-1493976040374-85c8e12f0c0e?w=1920&q=80");
  background-repeat: repeat;
  background-size: cover;
  background-attachment: fixed;
}

.search-page-container {
  min-height: 100vh;
}

.search-hero {
  background: linear-gradient(135deg, rgba(220, 20, 60, 0.9) 0%, rgba(26, 26, 26, 0.95) 100%), url("https://images.unsplash.com/photo-1492571350019-22de08371fd3?w=1920&q=80");
  background-size: cover;
  background-position: center;
  padding: 80px 0 60px;
  margin-bottom: 40px;
}

.search-hero-content {
  text-align: center;
  color: white;
}

.search-main-title {
  font-size: 3.5rem;
  font-weight: 700;
  margin-bottom: 15px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}
.search-main-title i {
  color: #ffd700;
}

.search-subtitle {
  font-size: 1.3rem;
  opacity: 0.95;
}

.title {
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
}

.search-body-modern {
  background: rgba(255, 255, 255, 0.98);
  backdrop-filter: blur(10px);
  border-radius: 25px;
  padding: 40px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.15);
  margin-bottom: 40px;
}

.search-section {
  background: linear-gradient(135deg, rgba(220, 20, 60, 0.05) 0%, rgba(255, 255, 255, 0) 100%);
  border-radius: 20px;
  padding: 30px;
  margin-bottom: 25px;
  border: 2px solid rgba(220, 20, 60, 0.1);
  transition: all 0.3s ease;
}
.search-section:hover {
  border-color: rgba(220, 20, 60, 0.3);
  box-shadow: 0 5px 20px rgba(220, 20, 60, 0.1);
}

.search-section-header {
  display: flex;
  align-items: center;
  gap: 15px;
  margin-bottom: 25px;
  padding-bottom: 15px;
  border-bottom: 2px solid rgba(220, 20, 60, 0.2);
}
.search-section-header i {
  font-size: 1.8rem;
  color: #dc143c;
}

.search-section-title {
  font-size: 1.5rem;
  font-weight: 700;
  color: #1a1a1a;
  margin: 0;
}

.checkbox-list-modern {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
}

.category-label-modern,
.area-label-modern,
.genre-label-modern,
.prefecture-label-modern,
.keyword-label-modern {
  display: inline-flex;
  align-items: center;
  padding: 12px 24px;
  background: white;
  border: 2px solid #e0e0e0;
  border-radius: 50px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-weight: 600;
  color: #555;
  position: relative;
}
.category-label-modern:hover,
.area-label-modern:hover,
.genre-label-modern:hover,
.prefecture-label-modern:hover,
.keyword-label-modern:hover {
  border-color: #dc143c;
  background: rgba(220, 20, 60, 0.05);
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(220, 20, 60, 0.2);
}
.category-label-modern input[type=checkbox],
.area-label-modern input[type=checkbox],
.genre-label-modern input[type=checkbox],
.prefecture-label-modern input[type=checkbox],
.keyword-label-modern input[type=checkbox] {
  display: none;
}
.category-label-modern .category-tag-text,
.category-label-modern .area-tag-text,
.category-label-modern .genre-tag-text,
.category-label-modern .prefecture-tag-text,
.category-label-modern .keyword-tag-text,
.area-label-modern .category-tag-text,
.area-label-modern .area-tag-text,
.area-label-modern .genre-tag-text,
.area-label-modern .prefecture-tag-text,
.area-label-modern .keyword-tag-text,
.genre-label-modern .category-tag-text,
.genre-label-modern .area-tag-text,
.genre-label-modern .genre-tag-text,
.genre-label-modern .prefecture-tag-text,
.genre-label-modern .keyword-tag-text,
.prefecture-label-modern .category-tag-text,
.prefecture-label-modern .area-tag-text,
.prefecture-label-modern .genre-tag-text,
.prefecture-label-modern .prefecture-tag-text,
.prefecture-label-modern .keyword-tag-text,
.keyword-label-modern .category-tag-text,
.keyword-label-modern .area-tag-text,
.keyword-label-modern .genre-tag-text,
.keyword-label-modern .prefecture-tag-text,
.keyword-label-modern .keyword-tag-text {
  display: flex;
  align-items: center;
  gap: 8px;
}
.category-label-modern .category-tag-text i,
.category-label-modern .area-tag-text i,
.category-label-modern .genre-tag-text i,
.category-label-modern .prefecture-tag-text i,
.category-label-modern .keyword-tag-text i,
.area-label-modern .category-tag-text i,
.area-label-modern .area-tag-text i,
.area-label-modern .genre-tag-text i,
.area-label-modern .prefecture-tag-text i,
.area-label-modern .keyword-tag-text i,
.genre-label-modern .category-tag-text i,
.genre-label-modern .area-tag-text i,
.genre-label-modern .genre-tag-text i,
.genre-label-modern .prefecture-tag-text i,
.genre-label-modern .keyword-tag-text i,
.prefecture-label-modern .category-tag-text i,
.prefecture-label-modern .area-tag-text i,
.prefecture-label-modern .genre-tag-text i,
.prefecture-label-modern .prefecture-tag-text i,
.prefecture-label-modern .keyword-tag-text i,
.keyword-label-modern .category-tag-text i,
.keyword-label-modern .area-tag-text i,
.keyword-label-modern .genre-tag-text i,
.keyword-label-modern .prefecture-tag-text i,
.keyword-label-modern .keyword-tag-text i {
  font-size: 1rem;
}

.category-tag:checked + .category-tag-text,
.area-tag:checked + .area-tag-text,
.genre-tag:checked + .genre-tag-text,
.prefecture-tag:checked + .prefecture-tag-text,
.keyword-tag:checked + .keyword-tag-text {
  color: #dc143c;
  font-weight: 700;
}

.category-label-modern input:checked ~ .category-tag-text,
.area-label-modern input:checked ~ .area-tag-text,
.genre-label-modern input:checked ~ .genre-tag-text,
.prefecture-label-modern input:checked ~ .prefecture-tag-text,
.keyword-label-modern input:checked ~ .keyword-tag-text {
  color: #dc143c;
  font-weight: 700;
}

.category-label-modern:has(input:checked),
.area-label-modern:has(input:checked),
.genre-label-modern:has(input:checked),
.prefecture-label-modern:has(input:checked),
.keyword-label-modern:has(input:checked) {
  background: linear-gradient(135deg, rgba(220, 20, 60, 0.1) 0%, rgba(220, 20, 60, 0.05) 100%);
  border-color: #dc143c;
  color: #dc143c;
  box-shadow: 0 4px 15px rgba(220, 20, 60, 0.3);
}

.category-label-modern.checked,
.area-label-modern.checked,
.genre-label-modern.checked,
.prefecture-label-modern.checked,
.keyword-label-modern.checked {
  background: linear-gradient(135deg, rgba(220, 20, 60, 0.1) 0%, rgba(220, 20, 60, 0.05) 100%);
  border-color: #dc143c;
  color: #dc143c;
  box-shadow: 0 4px 15px rgba(220, 20, 60, 0.3);
}
.category-label-modern.checked .category-tag-text,
.category-label-modern.checked .area-tag-text,
.category-label-modern.checked .genre-tag-text,
.category-label-modern.checked .prefecture-tag-text,
.category-label-modern.checked .keyword-tag-text,
.area-label-modern.checked .category-tag-text,
.area-label-modern.checked .area-tag-text,
.area-label-modern.checked .genre-tag-text,
.area-label-modern.checked .prefecture-tag-text,
.area-label-modern.checked .keyword-tag-text,
.genre-label-modern.checked .category-tag-text,
.genre-label-modern.checked .area-tag-text,
.genre-label-modern.checked .genre-tag-text,
.genre-label-modern.checked .prefecture-tag-text,
.genre-label-modern.checked .keyword-tag-text,
.prefecture-label-modern.checked .category-tag-text,
.prefecture-label-modern.checked .area-tag-text,
.prefecture-label-modern.checked .genre-tag-text,
.prefecture-label-modern.checked .prefecture-tag-text,
.prefecture-label-modern.checked .keyword-tag-text,
.keyword-label-modern.checked .category-tag-text,
.keyword-label-modern.checked .area-tag-text,
.keyword-label-modern.checked .genre-tag-text,
.keyword-label-modern.checked .prefecture-tag-text,
.keyword-label-modern.checked .keyword-tag-text {
  color: #dc143c;
  font-weight: 700;
}

.search-section-collapsible {
  margin-bottom: 25px;
}

.prefecture-check,
.keyword-check {
  display: none;
}

.collapsible-header {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 18px 25px;
  background: linear-gradient(135deg, rgba(220, 20, 60, 0.1) 0%, rgba(220, 20, 60, 0.05) 100%);
  border: 2px solid rgba(220, 20, 60, 0.2);
  border-radius: 15px;
  cursor: pointer;
  font-weight: 600;
  color: #1a1a1a;
  transition: all 0.3s ease;
}
.collapsible-header:hover {
  background: linear-gradient(135deg, rgba(220, 20, 60, 0.15) 0%, rgba(220, 20, 60, 0.1) 100%);
  border-color: rgba(220, 20, 60, 0.4);
}
.collapsible-header i {
  transition: transform 0.3s ease;
  color: #dc143c;
}

.prefecture-check:checked + .collapsible-header,
.keyword-check:checked + .collapsible-header {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  border-bottom: none;
}
.prefecture-check:checked + .collapsible-header i,
.keyword-check:checked + .collapsible-header i {
  transform: rotate(180deg);
}

.collapsible-content {
  max-height: 0;
  overflow: hidden;
  background: white;
  border: 2px solid rgba(220, 20, 60, 0.2);
  border-top: none;
  border-radius: 0 0 15px 15px;
  transition: all 0.4s ease;
  opacity: 0;
}

.prefecture-check:checked + .collapsible-header + .collapsible-content,
.keyword-check:checked + .collapsible-header + .collapsible-content {
  max-height: 1000px;
  padding: 25px;
  opacity: 1;
}

.search-actions {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-top: 40px;
  padding-top: 30px;
  border-top: 2px solid rgba(220, 20, 60, 0.1);
}

.btn-reset {
  display: inline-flex;
  align-items: center;
  gap: 10px;
  padding: 14px 35px;
  background: #6c757d;
  color: white;
  text-decoration: none;
  border-radius: 50px;
  font-weight: 600;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(108, 117, 125, 0.3);
}
.btn-reset:hover {
  background: #5a6268;
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(108, 117, 125, 0.4);
  color: white;
  text-decoration: none;
}

.btn-search {
  display: inline-flex;
  align-items: center;
  gap: 12px;
  padding: 14px 40px;
  background: linear-gradient(135deg, #dc143c 0%, #b71c1c 100%);
  color: white;
  border: none;
  border-radius: 50px;
  font-weight: 700;
  font-size: 1.1rem;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(220, 20, 60, 0.4);
  cursor: pointer;
}
.btn-search:hover {
  background: linear-gradient(135deg, #b71c1c 0%, #8b0000 100%);
  transform: translateY(-2px);
  box-shadow: 0 6px 25px rgba(220, 20, 60, 0.5);
}
.btn-search i {
  font-size: 1.1rem;
}

.search-results-container {
  margin-top: 50px;
}

.results-title {
  color: white;
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 30px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  gap: 15px;
}
.results-title i {
  color: #ffd700;
}

.places-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 30px;
  margin-bottom: 50px;
}

.place-card-modern {
  background: white;
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
}
.place-card-modern:hover {
  transform: translateY(-10px);
  box-shadow: 0 15px 40px rgba(220, 20, 60, 0.2);
}

.place-card-link-modern {
  text-decoration: none;
  color: inherit;
  display: block;
}
.place-card-link-modern:hover {
  text-decoration: none;
  color: inherit;
}

.place-image-container {
  position: relative;
  width: 100%;
  height: 220px;
  overflow: hidden;
}

.place-image-modern {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.place-card-modern:hover .place-image-modern {
  transform: scale(1.1);
}

.place-overlay-modern {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.3) 100%);
}

.place-category-badge {
  position: absolute;
  top: 15px;
  right: 15px;
  background: rgba(220, 20, 60, 0.95);
  color: white;
  padding: 8px 15px;
  border-radius: 20px;
  font-size: 0.85rem;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 6px;
  backdrop-filter: blur(10px);
}
.place-category-badge i {
  font-size: 0.9rem;
}

.place-card-body {
  padding: 20px;
}

.place-name {
  font-size: 1.2rem;
  font-weight: 700;
  color: #1a1a1a;
  margin-bottom: 10px;
  line-height: 1.4;
}

.place-location {
  display: flex;
  align-items: center;
  gap: 8px;
  color: #666;
  font-size: 0.95rem;
}
.place-location i {
  color: #dc143c;
}

.no-results {
  text-align: center;
  padding: 80px 20px;
  color: white;
}
.no-results i {
  font-size: 4rem;
  color: rgba(255, 255, 255, 0.5);
  margin-bottom: 20px;
}
.no-results h3 {
  font-size: 2rem;
  margin-bottom: 10px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
}
.no-results p {
  font-size: 1.1rem;
  opacity: 0.9;
}

@media (max-width: 768px) {
  .search-main-title {
    font-size: 2.5rem;
  }
  .search-body-modern {
    padding: 25px;
  }
  .search-section {
    padding: 20px;
  }
  .checkbox-list-modern {
    gap: 10px;
  }
  .places-grid {
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 20px;
  }
  .search-actions {
    flex-direction: column;
  }
  .search-actions .btn-reset,
  .search-actions .btn-search {
    width: 100%;
    justify-content: center;
  }
}